.wrapping-murottal {
  margin-top: 30px;
  padding: 10px 100px;  
  display: grid;
  justify-content: center;
}

.card-pembaca {
  width: 700px;
  display: flex;
  gap: 20px;
  /* background-color: #fff; */
  /* box-shadow: 0 0 8px 0px rgba(63, 189, 166, .4);  */
  padding: 5px;
  margin-bottom: 20px;
  border-radius: 75px 0px 0px 75px;
}

.pictProfile {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
}

.pictProfile img {
  width: 100%;
  border-radius: 50%;
}

.audio {
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.audio-murottal {
  width: 530px;
  height: 40px;
  outline: none;
  z-index: 1;
}

@media screen and (max-width: 768px) {
  .wrapping-murottal {
    padding: 10px;
    /* border: 1px solid black; */
  }

  .card-pembaca {
    width: 550px;
  }

  .audio-murottal {
    width: 400px;
  }
}

@media screen and (max-width: 576px) {
  .wrapping-murottal {
    padding: 0px;
    justify-content: center;
    align-items: center;
  }

  .card-pembaca {
    width: 100vw;
    box-sizing: border-box;
    gap: 10px;
  }

  .pictProfile {
    width: 15vw;
    height: 15vw;
  }

  .audio {
    padding: 5px;
    gap: 10px;
    font-size: 14px;
  }

  .audio-murottal {
    width: 75vw;
    height: 5vw;
  }
}