@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  text-decoration: none;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

body {
  position: relative;
}

.NavbarItems {
  background: linear-gradient(90deg, rgb(73, 197, 100) 0%, rgb(47, 185, 109) 100%);
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.05em;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  font-weight: 400;
}

.fa-arrow-circle-left {
  font-size: 1em;
  color: white;
  margin-right: 10px;
}

.fa-quran,
.fa-book-reader,
.fa-play-circle,
.fa-play {
  margin-left: 10px;
  font-size: 1em;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 20px;
  gap: 20px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
}

.nav-links {
  color: white;
  text-decoration: none;
  padding: 10px 24px;
}

.nav-links:hover {
  background-color: #6df7b2;
  border-radius: 4px;
  transition: all .3s ease-in;
}

.fa-bars {
  color: white;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 992px) {

  .nav-menu {
    display: flex;
    flex-direction: column;
    grid-gap: 0;
    gap: 0;
    width: 200px;
    min-height: 100vh;
    position: absolute;
    top: 80px;
    right: -100%;
    opacity: 1;
    transition: all .7s ease;
    overflow: hidden;
  }

  .nav-menu.active {
    background: rgb(48,185,108);
    right: 0;
    opacity: 1;
    transition: all .7s ease;
    z-index: 10;
  }

  .nav-links {
    text-align: center;
    padding: 30px 0px;
    width: 100%;
    display: table;
  }

  .nav-links:hover,
  .nav-links:active {
    background-color: #4cdf9a;
    border-radius: 0;
  }

  .fa-arrow-circle-left {
    font-size: 1em;
    color: white;
    margin-right: 10px;
  }

  .navbar-logo {
    position: absolute;
    left: 0;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 25px; 
    right: 25px;
    font-size: 25px;
    cursor: pointer;
  }

  .fa-times {
    color: white; 
    font-size: 30px;
  }

}
@font-face {
  font-family: 'Akaya Telivigala', cursive;
  src: url('https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap');
}

.board-search {
  width: 400px;
  height: 65px;
  padding: 10px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 0 4px 0px rgba(63, 189, 166, .4); 
  display: flex;
  flex-direction: column;
  grid-gap: 7px;
  gap: 7px;
  margin-top: 100px;
}

label {
  font-family: 'Montserrat', sans-serif;
  /* font-family: 'Akaya Telivigala', cursive; */
  font-weight: 500;
}

.input-keyword {
  width: 385px;
  padding: 5px;
  outline: none;
  border: 1px solid rgb(63, 189, 166);
  border-radius: 3px;
}

@media screen and (max-width: 576px) {
  .board-search {
    width: 250px;
  }

  .input-keyword {
    width: 230px;
  }
}
.wrapper-card {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  grid-gap: 25px;
  gap: 25px;
  padding: 10px 50px;
}

.card {
  padding: 10px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 0 4px 0px rgba(63, 189, 166, .4); 
  display: flex;
  flex-direction: column;
  grid-gap: 7px;
  gap: 7px;
  cursor: pointer;  
  font-family: 'Montserrat', sans-serif;
  transition: .4s all ease-out;
}

.card:hover {
  box-shadow: 0 0 10px 0px rgba(63, 189, 166, .4); 
}

.flex-center {
  padding: 20px 10px;
}

.not-found {
  display: flex;
  justify-content: center;
}

.wrapping-not-found {
  max-width: 100vh;
}

.quran-img {
  width: 100%;
}

.link {
  text-decoration: none;
  color: black;
}
/* CSS GRID */
/* Responsive for < 992px */
@media screen and (max-width: 992px) {
  .wrapper-card {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Responsive for Mobile*/
@media screen and (max-width : 768px) {
  .wrapper-card {
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
    gap: 10px;
  }

  .card {
    box-shadow: 0 0 4px 0px rgba(63, 189, 166, .4); 
  }
}
.title-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  margin-top: 100px;
}

.wrapping-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1px 25px;
  border-bottom: 1px solid black;
}

.bismillah-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  padding-top: 20px;
}

.bismillah {
  text-align: center;
  /* border: 1px solid black; */
}

.bismillah,
.ayat-text {
  font-size: 2.5em;
}

.ayyat-section {
  margin-top: 50px;
}

.ayat {
  border-bottom: 1px solid #000;
  padding: 10px 20px;
}

.ayat-text {
  display: flex;
  justify-content: space-between;
  text-align: right;
  line-height: 80px;
}

.number-ayyat {
  font-size: .7em;
}

.translate {
  font-size: 1.25em;
}


@media screen and (max-width: 576px ) {

  .bismillah,
  .ayat-text {
    font-size: 1.7em;
  }

  .ayat-text {
    line-height: 40px;
  }

  .translate {
    font-size: 1.05em;
  }
}
.tafsir {
  text-align: justify;
  font-size: 1.25em;
}
.wrapping-murottal {
  margin-top: 30px;
  padding: 10px 100px;  
  display: grid;
  justify-content: center;
}

.card-pembaca {
  width: 700px;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  /* background-color: #fff; */
  /* box-shadow: 0 0 8px 0px rgba(63, 189, 166, .4);  */
  padding: 5px;
  margin-bottom: 20px;
  border-radius: 75px 0px 0px 75px;
}

.pictProfile {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
}

.pictProfile img {
  width: 100%;
  border-radius: 50%;
}

.audio {
  padding: 10px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

.audio-murottal {
  width: 530px;
  height: 40px;
  outline: none;
  z-index: 1;
}

@media screen and (max-width: 768px) {
  .wrapping-murottal {
    padding: 10px;
    /* border: 1px solid black; */
  }

  .card-pembaca {
    width: 550px;
  }

  .audio-murottal {
    width: 400px;
  }
}

@media screen and (max-width: 576px) {
  .wrapping-murottal {
    padding: 0px;
    justify-content: center;
    align-items: center;
  }

  .card-pembaca {
    width: 100vw;
    box-sizing: border-box;
    grid-gap: 10px;
    gap: 10px;
  }

  .pictProfile {
    width: 15vw;
    height: 15vw;
  }

  .audio {
    padding: 5px;
    grid-gap: 10px;
    gap: 10px;
    font-size: 14px;
  }

  .audio-murottal {
    width: 75vw;
    height: 5vw;
  }
}
